import { Component, OnInit } from '@angular/core';
import{NgForm}from'@angular/forms';
import { connexionService } from 'src/app/services/service.connexion';

@Component({
  selector: 'app-inscription-form',
  templateUrl: './inscription-form.component.html',
  styleUrls: ['./inscription-form.component.scss']
})
export class InscriptionFormComponent implements OnInit {

  constructor(private connexion:connexionService) { }

  ngOnInit(): void {
  }
  onSubmitInscription(form : NgForm)
  {
 
     
      var inscription={
        nom : 'COMPTE INCOMLET',
        prenom:'COMPTE INCOMLET',
        email:form.value['email'],
        codepostale:form.value['codepostale'],
        rue:form.value['rue'],
        password:form.value['password'],
        tel:'0777777777',
        ville:form.value['ville']
      }
    
      this.connexion.creerCompte(inscription);
    
  }
}
